import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, MenuController, LoadingController } from 'ionic-angular';
import { MainPage } from '../main/main';
import { AuthProvider } from '../../providers/auth/auth';

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {
  
  email: string;
  password: string;
  loading: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, private menuCtrl: MenuController, public authService: AuthProvider, public loadingCtrl: LoadingController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');

    this.showLoader();
 
    //Check if already authenticated
/*     this.authService.checkAuthentication().then((res) => {
        console.log("Already authorized");
        this.loading.dismiss();
        this.navCtrl.setRoot(MainPage);
    }, (err) => {
        console.log("Not already authorized");
        this.loading.dismiss();
    });  */
  }

  login(){

    this.showLoader();

    let credentials = {
      email: this.email,
      password: this.password
  };

/*   this.authService.login(credentials).then((result) => {
      this.loading.dismiss();
      console.log(result);
      this.navCtrl.setRoot(MainPage);
  }, (err) => {
      this.loading.dismiss();
      console.log(err);
  }); */

    //Pusho MainPage sulla pila delle pagine e la setto come root in modo da non poter tornare indietro
    this.navCtrl.push(MainPage);
    this.navCtrl.setRoot(MainPage);

  }

  showLoader(){
    this.loading = this.loadingCtrl.create({
      content: 'Authenticating...'
    });

    this.loading.present();
  }

}



